// @ts-check

import { dijkstra } from "../modules/graph.js"
import { Array2d, Lib, V } from "../modules/index.js"

export const useExample = false

export const exampleInput = `\
#################
#...#...#...#..E#
#.#.#.#.#.#.#.#.#
#.#.#.#...#...#.#
#.#.#.#.###.#.#.#
#...#.#.#.....#.#
#.#.#.#.#.#####.#
#.#...#.#.#.....#
#.#.#####.#.###.#
#.#.#.......#...#
#.#.###.#####.###
#.#.#...#.....#.#
#.#.#.#####.###.#
#.#.#.........#.#
#.#.#.#########.#
#S#.............#
#################`

/** @typedef {ReturnType<typeof parseInput>} InputType */

export const parseInput = Array2d.parse

/**
 * @param {InputType} input
 */
export function part1(input) {
	const start = Array2d.traverse(input).find((v) => v.value === "S").pos
	const end = Array2d.traverse(input).find((v) => v.value === "E").pos

	const result = findOptimalRoute(input, start).find((v) => V.eq(v.value.pos, end))

	return result.distance
}

/**
 * @param {string[][]} input
 * @param {V.Vec2} start
 */
function findOptimalRoute(input, start) {
	return dijkstra(
		(x) =>
			[
				{ pos: V.add(x.pos, V.DIRS_4[x.dir]), cost: 1, dir: x.dir },
				{ pos: x.pos, cost: 1000, dir: Lib.mod(x.dir + 1, 4) },
				{ pos: x.pos, cost: 1000, dir: Lib.mod(x.dir - 1, 4) },
			].filter((v) => Array2d.get(input, v.pos) !== "#"),
		(x) => x.cost,
		[{ pos: start, cost: 0, dir: 1 }],
		(x) => `${x.pos[0]},${x.pos[1]},${x.dir}`,
	)
}

/**
 * @typedef {import("../modules/graph.js").PathItem<T>} PathItem
 * @template T
 */

/**
 * @param {InputType} input
 */
export function findPathWithPredecessors(input) {
	const start = Array2d.traverse(input).find((v) => v.value === "S").pos
	const end = Array2d.traverse(input).find((v) => v.value === "E").pos

	const result = findOptimalRoute(input, start).find((v) => V.eq(v.value.pos, end))

	/** @type {Set<PathItem<{pos: V.Vec2, cost: number, dir: number}>>} */
	const visitedSet = new Set()

	/** @param {PathItem<{pos: V.Vec2, cost: number, dir: number}>} item */
	function backtrack(item) {
		visitedSet.add(item)
		for (const p of item.predecessors) backtrack(p)
	}

	backtrack(result)

	const visited = Array.from(visitedSet).sort((a, b) => a.distance - b.distance)

	return { result, visited, start, end }
}

/**
 * @param {InputType} input
 */
export function part2(input) {
	const { visited } = findPathWithPredecessors(input)

	return visited
		.values()
		.distinct((x) => V.toString(x.value.pos))
		.count()
}
